'use strict';

/**
* Standard library aliases.
*
* @module @stdlib/namespace-aliases
*
* @example
* var aliases = require( '@stdlib/namespace-aliases' );
*
* var list = aliases();
* // returns [...]
*
* @example
* var aliases = require( '@stdlib/namespace-aliases' );
*
* var list = aliases( '@stdlib/math-base-special' );
* // returns [...]
*/

// MODULES //

var aliases = require( './main.js' );


// EXPORTS //

module.exports = aliases;
